.coverpage {
    width: 100%;
    height: 100%;
    text-align: center;
    background-position: center;
    position: absolute;
    /*color: rgb(0, 0, 0);*/
    background-color: rgb(235, 239, 245);

    opacity: 10px;
    overflow: hidden;
}

.form1 {
    width: 50%;
    height: 70%;
    opacity: 80%;
    top: 5%;
    left: 5%;
    position: absolute;
    padding: 120px 10px;
    font-size: large;
}

.form1 input {
    width: 100%;
    margin-bottom: 10px;
}

.form1 button {
    width: 100%;
    margin-bottom: 20px;
    padding-left: 50px;

}

.form1 input[type="text"] {
    border: none;
    border-bottom: 1px solid rgb(0, 0, 0);
    
    outline: none;
    height: 20px;
    color: rgb(0, 0, 0);
    font-size: 20px;
    padding: 5px;
}

.form1 button[type="submit"] {
    background-color: black;
    color: white;
    font-size: 15px;
    padding: 10px 10px;
    border-radius: 5px;
    margin:  10px;
    cursor: pointer;
    width: 150px;
    justify-content: center;
    
    
    
}

.buttonDelete {
    /*border:none;
    outline: none;
    height: 20%;
    background: rgb(0, 140, 255);
    color: rgb(0, 0, 0);
    font-style: inherit;
    padding-top: 5%;*/
    background-color: black;
    color: white;
    font-size: 15px;
    padding: 10px 15px;
    border-radius: 5px;
    margin: 10px 0px;
    cursor: pointer;
}

.app-container {
    width: 100%;
    height: 100%;
    background-color: rgb(235, 239, 245);
    background-size: cover;
    background-position: center;
    position: absolute;
    padding-right: 80px;
}

.usersbox {
    
    font-size: large;
}


.boxuser {
    width: 30%;
    height: 73%;
    padding: 120px 10px;
    top: 5%;
    left: 60%;
    right: 5%;
    position: absolute;
}